<script setup>
//Generales
import { useI18n } from 'vue-i18n'
import { ref, computed, onMounted } from 'vue'
import { API_BASE_URL } from '@/config/api'

const { t, locale } = useI18n();
const selectedOption = ref(1)
const temporalidad = ref(1)
const mesSeleccionado = ref(0)
const semanaSeleccionada = ref(0)
// Cantidad de audios
const countAudios = ref(0)

async function GetAudiosCount(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetCountAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ user: sesion, temporalidad: x })
    })

    const data = await response.json()

    if (data.status == 1) {
      countAudios.value = data.data
    } else {
      countAudios.value = 0
    }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

//Tiempo de escucha
const minutos = ref(0);
const segundos = ref(0);

async function GetTimeAudios(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetTimeAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ user: sesion, temporalidad: x })
    })

    const data = await response.json()

    if (data.status == 1) {

      const totalSegundos = data.data;
      // OpciÃ³n 1: Guardar en variables separadas
      minutos.value = Math.floor(totalSegundos / 60);
      segundos.value = Math.floor(totalSegundos % 60);
    } else {
      minutos.value = 0
      segundos.value = 0
    }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

//GrÃ¡fica de barras
import { Chart as ChartJS, BarElement, CategoryScale, LinearScale, Tooltip, Legend } from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels'
import { Bar } from 'vue-chartjs'

const daysLabels = ref([])
const daysData = ref([])
const drillSource = ref([])
const historyStack = ref([]);

ChartJS.register(BarElement, CategoryScale, LinearScale, Tooltip, Legend, ChartDataLabels)

const barData = computed(() => ({
  labels: daysLabels.value,
  datasets: [
    {
      label: '',
      data: daysData.value, // ðŸ”¹ ahora usa los datos dinÃ¡micos
      backgroundColor: '#AAAAAA',
      barThickness: 14,
      borderRadius: 10,
      pointStyle: 'circle',
    }
  ]
}))
const barOptions = {
  layout: {
    padding: {
      top: 10
    }
  },
  responsive: true,
  onClick: (evt, elements) => {
    if (!elements.length) return;

    const index = elements[0].index;
    const item = drillSource.value[index];

    if (!item) return;

    handleDrill(item);
  },
  plugins: {
    legend: {
      display: true,
      labels: {
        padding: 10,
        color: '#555',
        boxHeight: 0
      },
    },
    datalabels: {
      anchor: 'end',
      align: 'start',
      offset: -30,
      backgroundColor: '#000000',
      borderRadius: 999,
      color: '#ffffff',
      font: {
        weight: '500',
        size: 10,
      },
      padding: {
        top: 8,
        bottom: 8,
        left: 9,
        right: 9,
      },
      formatter: (value) => value,
    },
  },
  scales: {
    y: {
      beginAtZero: true,
      ticks: {
        display: false,
      },
      grid: {
        display: true
      },
    },
    x: {
      grid: {
        display: false,
      }
    },
  }
}

function handleDrill(item) {
  // Guardamos estado actual ANTES de cambiar
  historyStack.value.push({
    temporalidad: temporalidad.value,
    mes: mesSeleccionado.value,
    semana: semanaSeleccionada.value
  });

  // â¬‡ï¸ Si estÃ¡s viendo aÃ±o â†’ pasar a meses
  if (temporalidad.value == 3) {
    mesSeleccionado.value = item.dia;   // â† viene del backend
    temporalidad.value = 2;
    GetDays();
    return;
  }

  // â¬‡ï¸ Si estÃ¡s viendo mes â†’ pasar a semanas
  if (temporalidad.value == 2) {
    semanaSeleccionada.value = item.dia;  // â† viene del backend
    temporalidad.value = 1;
    GetDays();
    return;
  }
}

function goBack() {
  if (!historyStack.value.length) return;

  const previous = historyStack.value.pop();

  temporalidad.value = previous.temporalidad;
  mesSeleccionado.value = previous.mes;
  semanaSeleccionada.value = previous.semana;

  GetDays();
}

async function GetDays() {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetDays`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ user: sesion, temporalidad: temporalidad.value, anio: 0, mes: mesSeleccionado.value, semana: semanaSeleccionada.value })
    })

    const data = await response.json()

    if (data.status == 1 && data.data != "") {
      const parsedData = JSON.parse(data.data);
      drillSource.value = parsedData;

      if (temporalidad.value != 3) {
        daysLabels.value = parsedData.map(item => [
          item.nombre.substring(0, 3),
          item.dia
        ])
      }
      else {
        daysLabels.value = parsedData.map(item => [
          item.nombre.substring(0, 3)
        ])
      }

      daysData.value = parsedData.map(item => item.cantidadAudios)
    }
    else {
      daysLabels.value = []
      daysData.value = [0, 0, 0, 0, 0, 0, 0];
    }
  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

//Mapa de calor
const coloresMomentos = [
  "#efefef", // 0 â†’ Registro
  "#E3FCFF", // 1 â†’ Act
  "#FFE4B3", // 2 â†’ Mente
  "#FFC4B3", // 3 â†’ Lugares
  "#FF9797", // 4 â†’ Palabras
  "#fb5353"  // 5 â†’ Hablar
];
const diasOrden = ['Lunes', 'Martes', 'MiÃ©rcoles', 'Jueves', 'Viernes', 'SÃ¡bado', 'Domingo'];
const dataColorEs = ref({});
const dayMap = {
  'Lunes': 'Mon',
  'Martes': 'Tue',
  'MiÃ©rcoles': 'Wed',
  'Jueves': 'Thu',
  'Viernes': 'Fri',
  'SÃ¡bado': 'Sat',
  'Domingo': 'Sun'
};
// const columnsColor = computed(() => {
//   return locale.value === 'es'
//     ? diasOrden 
//     : diasOrden.map(dia => dayMap[dia]);
// });

const columnsColor = computed(() => {

  // Semana â†’ DÃ­as
  if (temporalidad.value == 1) {
    return diasOrden
  }

  // Mes â†’ Semanas
  if (temporalidad.value == 2) {
    return ["Sem 1", "Sem 2", "Sem 3", "Sem 4", "Sem 5"]
  }

  // AÃ±o â†’ Meses
  return ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul", "Ago", "Sep", "Oct", "Nov", "Dic"]
})

const dataColor = computed(() => {
  if (locale.value === 'es') return dataColorEs.value;

  const translated = {};
  for (const dia of diasOrden) {
    translated[dayMap[dia]] = dataColorEs.value[dia] || [];
  }
  return translated;
});

// const dataColor = computed(() => {

//   if (temporalidad.value === 1) {
//     if (locale.value === 'es') return dataColorEs.value;

//     const translated = {};
//     for (const dia of diasOrden) {
//       translated[dayMap[dia]] = dataColorEs.value[dia] || [];
//     }
//     return translated;
//   }

//   return dataColorEs.value;
// });


const maxRowsColor = computed(() => {
  return Math.max(...columnsColor.value.map(col => dataColor.value[col]?.length || 0), 0);
});
const getValueAt = (col, rowIndex) => {
  const item = dataColor.value[col]?.[rowIndex];
  return item ? item.intervalo : null;
};

// function getColorAt(col, rowIndex) {
//   const item = dataColor.value[col]?.[rowIndex];
//   if (!item) return "#fff";
//   return coloresMomentos[item.momento] || "#fff";
// }
function getColorAt(col, rowIndex) {
  const item = dataColor.value[col]?.[rowIndex];

  // Si no hay item â†’ blanco
  if (!item) return "#fff";

  // Si la cantidad es 0 â†’ blanco
  if (!item.cantidad || item.cantidad === 0) return "#fff";

  // Si hay dato â†’ pinta color
  return coloresMomentos[item.momento] || "#fff";
}

// async function GetMomentsAudios(x) {
