//             grouped[dia].push({ intervalo, momento });
//           }
//         }
//       });

//       dataColorEs.value = grouped;
//       console.log("Momentos agrupados:", grouped);
//     } else {
//       // ðŸ”¹ Siempre mantiene estructura de dÃ­as vacÃ­os
//       const empty = {};
//       diasOrden.forEach(dia => { empty[dia] = []; });
//       dataColorEs.value = empty;
//     }

//   } catch (error) {
//     console.error("Error al cargar audios:", error)
