async function GetMomentsAudios(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetMomentsAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ user: sesion, temporalidad: x })
    })

    const data = await response.json()
    console.log(data.data)
    if (data.status !== 1 || !data.data) {
      dataColorEs.value = {}
      return;
    }

    const parsed = JSON.parse(data.data)
    // -------------------------
    // ðŸŽ¯ SEMANA
    // -------------------------
    if (x == 1) {
      const grouped = {}
      diasOrden.forEach(d => grouped[d] = {})   // OBJETO, no array

      parsed.forEach(item => {
        const dia = item.Dia
        const clave = `${item.Intervalo}-${item.Momento}`
        // const intervalo = item.Intervalo

        if (!grouped[dia]) return

        if (!grouped[dia][clave]) {
          grouped[dia][clave] = {
            intervalo: item.Intervalo,
            momento: item.Momento,
            cantidad: 0
          }
        }

        grouped[dia][clave].cantidad += item.CantidadAudios
      })

      // Convertimos OBJETO â†’ ARRAY
      const result = {}
      diasOrden.forEach(d => {
        result[d] = Object.values(grouped[d])
      })

      dataColorEs.value = result
      return
    }

    if (x == 2) {
      const semanas = ["Sem 1", "Sem 2", "Sem 3", "Sem 4", "Sem 5"]

      const grouped = {}
      semanas.forEach(s => grouped[s] = {})

      parsed.forEach(item => {
        const key = "Sem " + item.Semana
        const clave = `${item.Intervalo}-${item.Momento}`
        //const intervalo = item.Intervalo

        if (!grouped[key]) return

        if (!grouped[key][clave]) {
          grouped[key][clave] = {
            intervalo: item.Intervalo,
            momento: item.Momento,
            cantidad: 0
          }
        }

        grouped[key][clave].cantidad += item.CantidadAudios
      })

      const result = {}
      semanas.forEach(s => {
        result[s] = Object.values(grouped[s])
      })

      dataColorEs.value = result
      return
    }

    // -------------------------
    // ðŸŽ¯ AÃ‘O  (Meses)
    // -------------------------


    if (x == 3) {
      const meses = ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul", "Ago", "Sep", "Oct", "Nov", "Dic"]

      // Primero organizamos por mes pero como OBJETO, no arreglo
      const grouped = {}
      meses.forEach(m => grouped[m] = {})

      parsed.forEach(item => {
        const mes = item.Label
        const clave = `${item.Intervalo}-${item.Momento}`
        //const intervalo = item.Intervalo

        if (!grouped[mes]) return

        // Si no existe ese intervalo aÃºn, crÃ©alo
        if (!grouped[mes][clave]) {
          grouped[mes][clave] = {
            intervalo: item.Intervalo,
            momento: item.Momento,
            cantidad: 0
          }
        }

        // Sumamos los audios
        grouped[mes][clave].cantidad += item.CantidadAudios
      })

      // ðŸ”¥ ImportantÃ­simo:
      // Tu componente espera ARRAYS, asÃ­ que convertimos de objeto â†’ arreglo
      const result = {}
      meses.forEach(m => {
        result[m] = Object.values(grouped[m])
      })

      dataColorEs.value = result
      return
    }

    //  if (x == 3){
    //       const meses = ["Ene","Feb","Mar","Abr","May","Jun","Jul","Ago","Sep","Oct","Nov","Dic"]

    //       const grouped = {}
    //       meses.forEach(m => grouped[m] = [])

    //       parsed.forEach(item => {
    //         const key = item.Label   

    //         if(!grouped[key]) return

    //         for(let i = 0; i < item.CantidadAudios; i++){
    //           grouped[key].push({
    //             intervalo: item.Intervalo,
    //             momento: item.Momento
    //           })
    //         }
    //       })

    //       dataColorEs.value = grouped
    //       return
    //     }

  } catch (e) {
    console.error("Error momentos", e)
  }
}

onMounted(() => {
  GetDatos(selectedOption.value)
})

function GetDatos(x) {
  temporalidad.value = x;
  mesSeleccionado.value = 0;
  semanaSeleccionada.value = 0;
  GetAudiosCount(x);
  GetTimeAudios(x);
  GetDays();
  GetMomentsAudios(x);
}
</script>

